
@import url("https://fonts.googleapis.com/css?family=Ubuntu");
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#header {
  background: url("assets/roraima.jpg");
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 800px;
  width: 100%;
}
#header h1 {
    font-family: 'Ubuntu', sans-serif;
    z-index: 99;
    -ms-flex-item-align: center;
        -ms-grid-row-align: center;
        align-self: center;
    color: #FFF;
    top: 0;
    width: 20%;
}
#header canvas {
    background-color: rgba(71, 129, 255, 0.2);
    position: absolute;
    top: 0;
    left: 0;
}

